import React from "react";
import { Link } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Card, ListGroup } from "react-bootstrap";

import useEditionData from "../../hooks/useEditionData";
import { Edition } from "../../data";

import * as styles from "./archive.module.css";

type editionCardProps = {
  edition: Edition;
};

const EditionCard: React.FC<editionCardProps> = ({ edition }) => {
  const { t } = useTranslation();
  const cardItems = ["schedule", "workshops", "conference", "partners"];
  return (
    <Card className={`card ${styles.card}`}>
      <Card.Header className={"cardHeader"}>{edition}</Card.Header>
      <Card.Body className={"cardBody"}>
        <ListGroup variant="flush">
          {cardItems.map((item, index) => (
            <ListGroup.Item
              action
              key={"lg-item" + index}
              as={Link}
              to={`/${edition}/${item}`}
            >
              {t(`navbar.${item}`)}
            </ListGroup.Item>
          ))}
          <ListGroup.Item
            action
            key={"lg-item leaderboard"}
            as={"a"}
            href={useEditionData(edition).leaderboard}
          >
            {t("navbar.leaderboard")}
          </ListGroup.Item>
        </ListGroup>
      </Card.Body>
    </Card>
  );
};

type ArchiveProps = {
  previousEditions: Edition[];
};

const Archive: React.FC<ArchiveProps> = ({ previousEditions }) => {
  const { t } = useTranslation();
  return (
    <div className={`page ${styles.archive}`} id={"archive"}>
      <h1 className={"title"}>{t("title")}</h1>
      <div className={`${styles.cards}`}>
        {previousEditions.map((edition) => (
          <EditionCard key={"editionCard" + edition} edition={edition} />
        ))}
      </div>
    </div>
  );
};

export default Archive;
